body {
    background-image: linear-gradient(
        150deg,
        rgba(white, 0.5),
        rgba(lightblue, 0.5)
    );

    min-height: 100vh;
}

.container-robot {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 80vh;
}

h1 {
    text-align: center;
    font-family: "Roboto", sans-serif;
    color: #5f93e8;
    margin-top: 5rem;
    font-size: 3rem;
}

.robots {
    display: flex;

    justify-content: center;
}

.head,
.left_arm,
.torso,
.right_arm,
.left_leg,
.right_leg {
    background-color: #5f93e8;
}

.head {
    width: 200px;
    margin: 0 auto;
    height: 150px;
    border-radius: 200px 200px 0 0;
    margin-bottom: 10px;
}

.eyes {
}

.head:hover {
    width: 300px;
    transition: 1s ease-in-out;
}

.upper_body {
    width: 300px;
    height: 150px;
    display: flex;
}

.left_arm,
.right_arm {
    width: 40px;
    height: 125px;
    border-radius: 100px;
}

.left_arm {
    margin-right: 10px;
    transition: all 0.5s;
}

.left_arm:hover {
    transform: rotate(20deg);
}

.right_arm {
    margin-left: 10px;
    transition: all 0.5s;
}

.right_arm:hover {
    transform: rotate(-20deg);
}
.torso {
    width: 200px;
    height: 200px;
    border-radius: 0 0 50px 50px;
}

.lower_body {
    display: flex;
    width: 200px;
    height: 200px;
    /* This is another useful property. Hmm what do you think it does?*/
    margin: 0 auto;
}

.left_leg,
.right_leg {
    width: 40px;
    height: 120px;
    border-radius: 0 0 100px 100px;
    transition: all 0.5s;
}

.left_leg {
    margin-left: 45px;
}

.left_leg:hover {
    -webkit-transform: rotate(20deg);
    -moz-transform: rotate(20deg);
    -o-transform: rotate(20deg);
    -ms-transform: rotate(20deg);
    transform: rotate(20deg);
}

.right_leg {
    margin-left: 30px;
}

.right_leg:hover {
    transform: rotate(-20deg);
}

.left_eye,
.right_eye {
    width: 20px;
    height: 20px;
    border-radius: 15px;
    background-color: white;
}

.left_eye {
    /* These properties are new and you haven't encountered
	in this course. Check out CSS Tricks to see what it does! */
    position: relative;
    top: 100px;
    left: 40px;
}

.right_eye {
    position: relative;
    top: 80px;
    left: 140px;
}
